"use client";

import { useState, useEffect, useRef } from "react";
import Link from "next/link";
import Image from "next/image";
import { useRouter, usePathname } from "next/navigation";
import { 
  motion, 
  AnimatePresence, 
  useScroll, 
  useMotionValueEvent 
} from "framer-motion";
import { Menu, X, ShoppingBag, User, Heart, Search, ArrowRight } from "lucide-react";
import { useCart } from "@/lib/cart-store";
import { useWishlist } from "@/lib/wishlist-store";
import { cn } from "@/lib/utils"; 

// --- ANIMATION VARIANTS ---
const navVariants = {
  hidden: { y: -100, opacity: 0 },
  visible: { 
    y: 0, 
    opacity: 1,
    transition: { duration: 0.6, ease: [0.22, 1, 0.36, 1] } 
  },
};

const mobileMenuVariants = {
  closed: { opacity: 0, y: "-100%" },
  open: { 
    opacity: 1, 
    y: 0,
    transition: { duration: 0.5, ease: [0.22, 1, 0.36, 1] }
  }
};

const linkContainerVariants = {
  open: { transition: { staggerChildren: 0.1, delayChildren: 0.2 } },
  closed: { transition: { staggerChildren: 0.05, staggerDirection: -1 } }
};

const linkItemVariants = {
  open: { y: 0, opacity: 1 },
  closed: { y: 20, opacity: 0 }
};

export default function Navigation() {
  // State
  const [isOpen, setIsOpen] = useState(false); // Mobile Menu
  const [isSearchOpen, setIsSearchOpen] = useState(false); // Search Toggle
  const [searchQuery, setSearchQuery] = useState("");
  const [hoveredIndex, setHoveredIndex] = useState<number | null>(null);
  const [scrolled, setScrolled] = useState(false);
  
  // Hooks
  const { scrollY } = useScroll();
  const pathname = usePathname();
  const router = useRouter();
  const searchInputRef = useRef<HTMLInputElement>(null);
  
  // Stores
  const { items } = useCart();
  const { getCount: getWishlistCount } = useWishlist();
  
  const itemCount = items.reduce((sum, item) => sum + item.quantity, 0);
  const wishlistCount = getWishlistCount();

  // Scroll Handler
  useMotionValueEvent(scrollY, "change", (latest) => {
    setScrolled(latest > 20);
  });

  // Body Lock
  useEffect(() => {
    if (isOpen) document.body.style.overflow = "hidden";
    else document.body.style.overflow = "unset";
  }, [isOpen]);

  // Auto-focus search input when opened
  useEffect(() => {
    if (isSearchOpen && searchInputRef.current) {
      setTimeout(() => searchInputRef.current?.focus(), 100);
    }
  }, [isSearchOpen]);

  // Handle Search Submit
  const handleSearchSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (searchQuery.trim()) {
      router.push(`/shop?q=${encodeURIComponent(searchQuery)}`);
      setIsSearchOpen(false); // Optional: close on submit
    }
  };

  const navLinks = [
    { href: "/shop", label: "Shop" },
    { href: "/size-finder", label: "Size Finder" },
    { href: "/craftsmanship", label: "Craftsmanship" },
    { href: "/about", label: "About" },
  ];

  return (
    <>
      <motion.nav
        variants={navVariants}
        initial="hidden"
        animate="visible"
        className={cn(
          "fixed top-0 inset-x-0 z-50 transition-all duration-300 ease-in-out border-b",
          scrolled 
            ? "bg-background/80 backdrop-blur-xl border-border/50 shadow-sm py-2" 
            : "bg-background border-transparent py-4"
        )}
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center gap-4 relative">
            
            {/* --- 1. LOGO --- */}
            <Link href="/" className="flex items-center gap-2 group z-50 shrink-0">
              <motion.div 
                whileHover={{ rotate: 10, scale: 1.1 }}
                transition={{ type: "spring", stiffness: 300 }}
              >
                <Image
                  src="/icoo.png"
                  alt="Banglerd logo"
                  width={35}
                  height={35}
                  className="object-contain"
                />
              </motion.div>
              <span className="font-serif text-2xl font-bold bg-gradient-to-r from-primary to-foreground bg-clip-text text-transparent hidden min-[350px]:block">
                anglerd
              </span>
            </Link>

            {/* --- 2. DESKTOP NAV (Hidden if search expands too wide on tablet) --- */}
            <div className={cn(
              "hidden lg:flex items-center bg-muted/30 px-2 py-1.5 rounded-full border border-border/40 backdrop-blur-sm transition-all duration-300",
              isSearchOpen ? "opacity-0 pointer-events-none scale-95 hidden xl:flex" : "opacity-100 scale-100"
            )}>
              {navLinks.map((link, index) => (
                <Link
                  key={link.href}
                  href={link.href}
                  onMouseEnter={() => setHoveredIndex(index)}
                  onMouseLeave={() => setHoveredIndex(null)}
                  className={cn(
                    "relative px-4 py-2 text-sm font-medium transition-colors duration-200 z-10",
                    pathname === link.href ? "text-primary font-semibold" : "text-foreground/80 hover:text-foreground"
                  )}
                >
                  <AnimatePresence>
                    {hoveredIndex === index && (
                      <motion.span
                        className="absolute inset-0 bg-background rounded-full shadow-sm border border-border/50"
                        layoutId="hoverBackground"
                        initial={{ opacity: 0, scale: 0.8 }}
                        animate={{ opacity: 1, scale: 1 }}
                        exit={{ opacity: 0, scale: 0.8 }}
                        transition={{ type: "spring", bounce: 0.2, duration: 0.6 }}
                      />
                    )}
                  </AnimatePresence>
                  <span className="relative z-10">{link.label}</span>
                </Link>
              ))}
            </div>

            {/* --- 3. ICONS & SEARCH ACTION --- */}
            <div className="flex items-center gap-1 sm:gap-2">

              {/* === THE ANIMATED SEARCH BAR === */}
              <div className="flex items-center justify-end relative">
                <AnimatePresence initial={false}>
                  {isSearchOpen ? (
                    <motion.form
                      initial={{ width: 40, opacity: 0 }}
                      animate={{ width: "100%", opacity: 1 }}
                      exit={{ width: 40, opacity: 0 }}
                      transition={{ type: "spring", stiffness: 300, damping: 30 }}
                      onSubmit={handleSearchSubmit}
                      className={cn(
                         "absolute right-0 z-50 flex items-center bg-background border border-primary/20 rounded-full shadow-lg overflow-hidden",
                         // Responsive Widths:
                         "w-[260px] sm:w-[300px] md:w-[400px]"
                      )}
                    >
                      <Search className="w-4 h-4 text-muted-foreground ml-3 shrink-0" />
                      <input
                        ref={searchInputRef}
                        type="text"
                        placeholder="Search jewelry..."
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        className="w-full bg-transparent border-none focus:ring-0 text-sm px-3 py-2 outline-none placeholder:text-muted-foreground/50"
                      />
                      <button 
                         type="button"
                         onClick={() => { setIsSearchOpen(false); setSearchQuery(""); }}
                         className="p-2 hover:bg-muted text-muted-foreground transition-colors"
                      >
                         <X className="w-4 h-4" />
                      </button>
                    </motion.form>
                  ) : null}
                </AnimatePresence>

                {/* Search Trigger Button */}
                <motion.button 
                   whileTap={{ scale: 0.9 }}
                   onClick={() => setIsSearchOpen(true)}
                   className={cn(
                     "p-2.5 rounded-full transition-all duration-300 hover:bg-muted group",
                     isSearchOpen ? "opacity-0 pointer-events-none" : "opacity-100"
                   )}
                >
                   <Search className="w-5 h-5 group-hover:text-primary transition-colors" />
                </motion.button>
              </div>
              {/* === END SEARCH === */}

              {/* Wishlist */}
              <Link href="/account?tab=wishlist" className="relative p-2.5 hover:bg-muted rounded-full transition-colors hidden sm:flex group">
                 <Heart className="w-5 h-5 group-hover:text-red-500 transition-colors" />
                 <AnimatePresence>
                   {wishlistCount > 0 && (
                     <motion.span 
                       key="wishlist-badge"
                       initial={{ scale: 0 }} animate={{ scale: 1 }} exit={{ scale: 0 }}
                       className="absolute top-1 right-1 w-4 h-4 bg-red-500 text-white text-[10px] rounded-full flex items-center justify-center font-bold shadow-sm"
                     >
                       {wishlistCount}
                     </motion.span>
                   )}
                 </AnimatePresence>
              </Link>

              {/* Cart */}
              <Link href="/cart" className="relative p-2.5 hover:bg-muted rounded-full transition-colors group">
                <ShoppingBag className="w-5 h-5 group-hover:text-primary transition-colors" />
                <AnimatePresence>
                  {itemCount > 0 && (
                    <motion.span
                      key={itemCount}
                      initial={{ scale: 0.5, y: -5 }}
                      animate={{ scale: 1, y: 0 }}
                      className="absolute top-0 right-0 w-4 h-4 bg-primary text-primary-foreground text-[10px] rounded-full flex items-center justify-center font-bold shadow-sm"
                    >
                      {itemCount}
                    </motion.span>
                  )}
                </AnimatePresence>
              </Link>

              {/* Mobile Toggle */}
              <motion.button
                whileTap={{ scale: 0.9 }}
                onClick={() => setIsOpen(!isOpen)}
                className="lg:hidden p-2 text-foreground z-50"
              >
                {isOpen ? <X className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
              </motion.button>
            </div>
          </div>
        </div>
      </motion.nav>

      {/* --- MOBILE MENU OVERLAY --- */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
            variants={mobileMenuVariants}
            initial="closed"
            animate="open"
            exit="closed"
            className="fixed inset-0 bg-background z-40 lg:hidden flex flex-col pt-28 px-6 overflow-y-auto"
          >
            {/* Mobile Search Input */}
            <motion.form 
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.1 }}
              onSubmit={(e) => {
                handleSearchSubmit(e);
                setIsOpen(false);
              }}
              className="relative w-full mb-8"
            >
              <input 
                type="text" 
                placeholder="Search..." 
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="w-full bg-muted/50 border border-border rounded-xl px-4 py-4 pl-12 text-lg focus:ring-2 focus:ring-primary/20 outline-none transition-all"
              />
              <Search className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-muted-foreground" />
              <button type="submit" className="absolute right-4 top-1/2 -translate-y-1/2 p-2 bg-primary rounded-lg text-primary-foreground">
                <ArrowRight className="w-4 h-4" />
              </button>
            </motion.form>

            <motion.div 
              variants={linkContainerVariants}
              initial="closed"
              animate="open"
              exit="closed"
              className="flex flex-col gap-6"
            >
              {navLinks.map((link) => (
                <motion.div key={link.href} variants={linkItemVariants}>
                  <Link
                    href={link.href}
                    onClick={() => setIsOpen(false)}
                    className="flex items-center justify-between text-3xl font-serif font-medium hover:text-primary transition-colors group"
                  >
                    {link.label}
                    <ArrowRight className="-translate-x-4 opacity-0 group-hover:translate-x-0 group-hover:opacity-100 transition-all duration-300 w-6 h-6 text-primary" />
                  </Link>
                </motion.div>
              ))}

              <motion.div variants={linkItemVariants} className="h-px w-full bg-border my-2" />

              <motion.div variants={linkItemVariants} className="grid grid-cols-2 gap-4">
                 <Link href="/account" onClick={() => setIsOpen(false)} className="flex flex-col items-center justify-center gap-2 p-4 bg-muted/30 rounded-xl hover:bg-muted transition-colors">
                    <User className="w-6 h-6" /> 
                    <span className="text-sm font-medium">Account</span>
                 </Link>
                 <Link href="/account?tab=wishlist" onClick={() => setIsOpen(false)} className="flex flex-col items-center justify-center gap-2 p-4 bg-muted/30 rounded-xl hover:bg-muted transition-colors">
                    <Heart className="w-6 h-6" /> 
                    <span className="text-sm font-medium">Wishlist</span>
                 </Link>
              </motion.div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
}